.hide {
  opacity: 0;
  visibility: hidden;
  -webkit-transition-duration: 0.2s;
       -o-transition-duration: 0.2s;
          transition-duration: 0.2s;
}

.show {
  opacity: 1;
  visibility: visible;
  -webkit-transition-duration: 0.3s;
       -o-transition-duration: 0.3s;
          transition-duration: 0.3s;
}

.menusymbol {
  position: absolute;
  top: 6px;
  right: 28px;
  font-size: 2.8rem;
}

ul {
  position: absolute;
  top: 5px;
  display: -ms-flexbox;
  display: flex;
  height: 50px;
  margin: 0;
  padding-left: 25px;
  -ms-flex-pack: start;
      justify-content: flex-start;
  -ms-flex-align: center;
      align-items: center;
  -webkit-transition-duration: 1s;
       -o-transition-duration: 1s;
          transition-duration: 1s;
  z-index: 1;
}

ul a {
  color: #000;
  position: relative;
  text-decoration: none;
}

.closeBtn {
  top: -3px;
  font-size: 2rem;
  cursor: pointer;
  -webkit-transition-duration: 0.2s;
       -o-transition-duration: 0.2s;
          transition-duration: 0.2s;
}

.closeBtn:hover {
  -webkit-transform: scale(1.1);
      -ms-transform: scale(1.1);
          transform: scale(1.1);
}

li {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  height: 50px;
  width: 70px;
  margin-right: 0px;
  list-style-type: none;
  position: relative;
}


